@import '~@styles/index.scss';
.AccountDetail{
  height:100%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  .content{
    height:100%;
    margin-top: 10px;
    // background: white;
    border-top:1px solid #E3E3E3;
    .selectmode{
      display: flex;
      flex-grow: 1;
      flex-shrink: 1;

      li{
        background: #F5F5F5;
        color: #959595;
        &.active{
          color:$yellow;
          background: white;
        }
        display: flex;
        justify-content: center;
        align-items: center;
        height:40px;
        width:100%;
      }
    }
  }


}
